import { Component, h, Element, Prop } from '@stencil/core';

@Component({
    tag: 'ks-product-inquire',
    styleUrl: 'product-inquire.css'
})
export class ProductInquire {
    @Element() root: HTMLElement;
    @Prop() name: string;
    @Prop() producturl: string;
    
    dialog: HTMLKsDialogElement;

    componentDidRender() {
        this.dialog = this.root.querySelector("ks-dialog");
        const button = this.root.parentElement.querySelector("ks-product-inquire > *:first-child");
        button.addEventListener("click", ()=>{
            this.dialog.show();
        });
    }

	render() {
        const email = "sklep@kuchniasklep.pl";

        const subject = encodeURIComponent("Zapytanie o czas wysyłki: {name}".replace("{name}", this.name));
        const body = encodeURIComponent("Dzień dobry, \r\n\r\n Chcę poznać dokładny czas wysyłki dla produktu: \r\n {name} \r\n ({producturl})"
            .replace("{producturl}", this.producturl)
            .replace("{name}", this.name)
        );

        const mailto = `mailto:${email}?subject=${subject}&body=${body}`;

		return [
            <slot/>,
            <ks-dialog dark>
                <div>
                <div class="info">
                    <h3>Zapytaj o dokładny czas wysyłki</h3>
                    <p>Ten produkt jest dostępny z wydłużonym czasem realizacji.
                        Skontaktuj się z nami pod <strong>sklep@kuchniasklep.pl</strong> aby poznać dokładny czas wysyłki
                        lub wygeneruj gotową wiadomość z zapytaniem poniżej.
                    </p>
                    <p><em>Postaramy się odpowiedzieć jak najszybciej. Jeżeli nie odpowiemy w ciągu 2 dni roboczych prosimy o kontakt telefoniczny.</em></p>
                </div>

                <ks-button rounded link={mailto} name="Wygeneruj wiadomość" />
                </div>
            </ks-dialog>
        ];
    }

}
