ks-product-attribute {
    display: flex;
    align-items: center;
    margin: 5px 0px;

    font-family: var(--font-emphasis);
    font-size: 15px;
    line-height: 15px;
}

ks-product-attribute[danger] {
    color: var(--color-secondary);
    font-weight: 700;

    animation: blink 1.5s ease-in-out infinite;
}

ks-product-attribute[emphasis] {
    color: var(--color-secondary);
    font-weight: 700;
}

ks-product-attribute a {
    display: flex;
    align-items: center;

    color: inherit !important;
    text-decoration: none !important;
}

ks-product-attribute ks-icon {
    margin-right: 10px;
}

ks-product-attribute ks-icon:nth-child(2) {
    margin-left: 10px;
}


@media only screen and (max-width: 540px) {
    ks-product-attribute {
        display: flex;
        justify-content: center;
        text-align: center;
        align-items: center;
        margin: 5px auto;
    }
}

@media only screen and (max-width: 405px) {
    ks-product-attribute {
        max-width: 68%;
    }
}


ks-product-attribute .energy {
    display: inline-flex;
    align-items: center;
    height: 30px;
    box-sizing: border-box;
    margin-right: 5px;
}

ks-product-attribute .energy .letter {
    border: solid 1px #000000;
    border-left: none;
    border-right: none;
    height: 30px;
    line-height: 26px;
    box-sizing: border-box;
    font-family: var(--font-emphasis);
    font-weight: 700;
    font-weight: 700;
    padding: 0 7px 2px 12px;
}

ks-product-attribute .energy .range {
    padding-right: 2px;
    border: solid 1px #000000;
    border-right: none;
}

ks-product-attribute .energy .range > .triangle {
    width: 0px;
    height: 0px;
    border-style: solid;
    border-width: 0 4px 4px 4px;
    border-color: transparent transparent #000000 transparent;
    transform: rotate(0deg);
    margin-left: 1px;
 }

ks-product-attribute .energy .range div:not(.triangle) {
    margin-left: 1.5px;
    font-size: 11px;
    line-height: 12px;
 }

ks-product-attribute .energy > .triangle {
    width: 0px;
    height: 0px;
    border-style: solid;
    border-width: 15px 0 15px 16px;
    border-color: transparent transparent transparent #000000;
    position: relative;
    transform: translateX(-1px);

}

ks-product-attribute .energy .inner-triangle {
    width: 0px;
    height: 0px;
    border-style: solid;
    border-width: 14px 0 14px 15px;
    border-color: transparent transparent transparent #ffffff;
    position: absolute;
    top: -14px;
    left: -16px;
}

ks-product-attribute[energy="A"] .energy .inner-triangle { border-left-color: #009640; }
ks-product-attribute[energy="B"] .energy .inner-triangle { border-left-color: #52ae32; }
ks-product-attribute[energy="C"] .energy .inner-triangle { border-left-color: #c8d400; }
ks-product-attribute[energy="D"] .energy .inner-triangle { border-left-color: #ffed00; }
ks-product-attribute[energy="E"] .energy .inner-triangle { border-left-color: #fbba00; }
ks-product-attribute[energy="F"] .energy .inner-triangle { border-left-color: #ec6608; }
ks-product-attribute[energy="G"] .energy .inner-triangle { border-left-color: #e30613; }

ks-product-attribute[energy="A"] .energy > .letter { background-color: #009640; color: #ffffff; }
ks-product-attribute[energy="B"] .energy > .letter { background-color: #52ae32; color: #ffffff; }
ks-product-attribute[energy="C"] .energy > .letter { background-color: #c8d400; color: #000000; }
ks-product-attribute[energy="D"] .energy > .letter { background-color: #ffed00; color: #000000; }
ks-product-attribute[energy="E"] .energy > .letter { background-color: #fbba00; color: #000000; }
ks-product-attribute[energy="F"] .energy > .letter { background-color: #ec6608; color: #ffffff; }
ks-product-attribute[energy="G"] .energy > .letter { background-color: #e30613; color: #ffffff; }


ks-product-attribute[energy-old] .energy .range {
    display: none;
}

ks-product-attribute[energy-old] .energy .letter {
    border-left: solid 1px #000000;
}

ks-product-attribute[energy-old][energy="A+++"] .energy .inner-triangle { border-left-color: #00a64f; }
ks-product-attribute[energy-old][energy="A++"] .energy .inner-triangle { border-left-color: #4cbd35; }
ks-product-attribute[energy-old][energy="A+"] .energy .inner-triangle { border-left-color: #b0d917; }
ks-product-attribute[energy-old][energy="A"] .energy .inner-triangle { border-left-color: #fff000; }
ks-product-attribute[energy-old][energy="B"] .energy .inner-triangle { border-left-color: #f8b00a; }
ks-product-attribute[energy-old][energy="C"] .energy .inner-triangle { border-left-color: #f35c17; }
ks-product-attribute[energy-old][energy="D"] .energy .inner-triangle { border-left-color: #eb1923; }

ks-product-attribute[energy-old][energy="A+++"] .energy > .letter { background-color: #00a64f; color: #ffffff; }
ks-product-attribute[energy-old][energy="A++"] .energy > .letter { background-color: #4cbd35; color: #ffffff; }
ks-product-attribute[energy-old][energy="A+"] .energy > .letter { background-color: #b0d917; color: #000000; }
ks-product-attribute[energy-old][energy="A"] .energy > .letter { background-color: #fff000; color: #000000; }
ks-product-attribute[energy-old][energy="B"] .energy > .letter { background-color: #f8b00a; color: #ffffff; }
ks-product-attribute[energy-old][energy="C"] .energy > .letter { background-color: #f35c17; color: #ffffff; }
ks-product-attribute[energy-old][energy="D"] .energy > .letter { background-color: #eb1923; color: #ffffff; }