ks-product-inquire {
    display: block;
    position: relative;
}

ks-product-inquire .info {
    text-align: center;
}

ks-product-inquire h3 {
    font-family: var(--font-emphasis);
    font-size: 20px;
    font-weight: 700;
}

ks-product-inquire em {
    color: #8c8c8c;
}

ks-product-inquire > ks-button {
    width: fit-content;
    margin-bottom: 10px;
    margin-top: -5px;
}
ks-product-inquire > ks-button > * {
    font-weight: 700;
    font-family: var(--font-emphasis);
}