import { Component, h, Prop } from '@stencil/core';

@Component({
	tag: 'ks-product-attribute',
	styleUrl: 'product-attribute.css'
})
export class ProductAttribute {
    @Prop() href: string = "";
    
    @Prop() icon: string = "";
    @Prop({reflect: true}) energy: "A+++"|"A++"|"A+"|"A"|"B"|"C"|"D"|"E"|"F"|"G" = null;
    @Prop({reflect: true}) energyOld: boolean = false;
    @Prop() size: number = 1.0;

    @Prop({reflect: true}) danger: boolean = false;
    @Prop({reflect: true}) emphasis: boolean = false;

	render() {
        const content = 
        this.energy ? [
            <div class="energy">
                <div class="range">
                    <div>A</div>
                    <div class="triangle"/>
                    <div>G</div>
                </div>
                <div class="letter">{this.energy}</div>
                <div class="triangle">
                    <div class="inner-triangle"/>
                </div>
            </div>,
            <slot/>
        ] : [
            <ks-icon name={this.icon} size={this.size} aria-hidden />,
            <slot/>
        ];

        if(this.href != "")
            return <a href={this.href} target="_blank">{content}</a>;
		return content;
	}
}